import React from 'react'
import { ModalFooter, Modal, ModalHeader, ModalBody, Button } from 'reactstrap'

interface Props {
    isOpen: boolean
    handleClose: () => void,
    handleDelete: () => void,
}

interface State {
}

export default class DeleteTodoListModal extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props)
    }

    public render() {
        return (
            <Modal backdrop={"static"} fade={false} isOpen={this.props.isOpen} toggle={this.props.handleClose}>
                <ModalHeader toggle={this.props.handleClose}>Confirm</ModalHeader>
                <ModalBody>
                    You're about to remove a list that contains items. This action
                    cannot be undone. Do you want to continue?
                </ModalBody>
                <ModalFooter>
                    <Button color="danger" onClick={this.props.handleDelete}>
                        Delete list
                    </Button>

                    <Button color="secondary" onClick={this.props.handleClose}>
                        Cancel
                    </Button>
                </ModalFooter>
            </Modal>
        )
    }

}
