import React, { Dispatch, Fragment } from 'react'
import TodoListName from './TodoListName'
import ListRenamingInput from './ListRenamingInput'
import { ActionType } from 'typesafe-actions'
import { AppActions } from '../redux/actions'
import { RootState } from '../redux/store'
import { List } from '../model/models'
import { connect } from 'react-redux'
import DeleteTodoListModal from './DeleteTodoListModal'

function mapStateToProps(state: RootState) {
    return {
        // Cannot delete last list
        disableDelete: state.lists.length === 1
    }
}

function mapDispToProps(disp: Dispatch<ActionType<typeof AppActions>>) {
    return {
        renameList: (listId: string, name: string) =>
            disp(AppActions.renameList({ listId, name })),

        deleteList: (listId: string) =>
            disp(AppActions.deleteList({ listId })),
    }
}

type Props = ReturnType<typeof mapDispToProps>
    & ReturnType<typeof mapStateToProps>
    & { list: List }

type State = {
    renamingList: boolean,
    deleteModalVisible: boolean,
}

class TodoListHeader extends React.Component<Props, State> {

    readonly state = {
        renamingList: false,
        deleteModalVisible: false,
    }

    constructor(props: Props) {
        super(props)

        this.beginRenameList = this.beginRenameList.bind(this)
        this.endRenameList = this.endRenameList.bind(this)
        this.toggleDeleteModalVisiblity = this.toggleDeleteModalVisiblity.bind(this)
    }

    public render() {

        return (
            <Fragment>

                <DeleteTodoListModal
                    isOpen={this.state.deleteModalVisible}
                    handleClose={this.toggleDeleteModalVisiblity}
                    handleDelete={() => {
                        this.props.deleteList(this.props.list.id)
                        this.toggleDeleteModalVisiblity()
                    }} />

                {this.state.renamingList &&
                    <ListRenamingInput
                        currentListName={this.props.list.listName}
                        handleEndRename={this.endRenameList} />
                }

                {this.state.renamingList === false &&
                    <TodoListName
                        listName={this.props.list.listName}
                        handleBeginRename={this.beginRenameList}
                        deleteDisabled={this.props.disableDelete}
                        onDelete={() => {
                            if (this.props.list.items.length > 0) {
                                this.toggleDeleteModalVisiblity()
                            } else {
                                this.props.deleteList(this.props.list.id)
                            }
                        }}
                    />
                }
            </Fragment>
        )
    }

    private beginRenameList() {
        this.setState({
            renamingList: true,
        })
    }

    private endRenameList(newListName: string) {
        if (newListName !== "") {
            this.props.renameList(this.props.list.id, newListName)
        }

        this.setState({
            renamingList: false,
        })
    }

    private toggleDeleteModalVisiblity() {
        this.setState((state) => {
            return {
                deleteModalVisible: !state.deleteModalVisible
            }
        })
    }

}

export default connect(mapStateToProps, mapDispToProps)(TodoListHeader)
