import React from 'react'
import { Input } from 'reactstrap'

interface Props {
    currentListName: string,
    handleEndRename: (newListName: string) => void
}

interface State {
    newListName: string,
}

export default class ListRenamingInput extends React.Component<Props, State> {

    readonly state = {
        newListName: this.props.currentListName
    }

    constructor(props: Props) {
        super(props)

        this.handleInput = this.handleInput.bind(this)
    }

    public render() {
        return (
            <div className="mb-4 d-flex flex-column">
                <Input
                    autoFocus={true}
                    value={this.state.newListName}
                    onChange={this.handleInput}
                    onKeyDown={(event: React.KeyboardEvent<HTMLInputElement>) => {
                        if (event.key === "Enter") {
                            this.props.handleEndRename(this.state.newListName)
                        }
                    }}
                    onBlur={() => {
                        this.props.handleEndRename(this.state.newListName)
                    }}
                />
            </div>
        )
    }

    private handleInput(event: any) {
        const input = event.target.value as string

        this.setState({
            newListName: input
        })
    }

}
