import React from 'react'
import { Button } from 'reactstrap'

interface Props {
    listName: string,
    handleBeginRename: () => void,
    deleteDisabled: boolean,
    onDelete: () => void
}

interface State {
}

export default class TodoListName extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props)
    }

    public render() {
        return (
            <div className="mb-4 d-flex align-items-center">
                <h2 className="w-100" style={{ cursor: "pointer" }} onClick={this.props.handleBeginRename}>
                    {this.props.listName}
                </h2>

                <Button
                    className="ml-2"
                    disabled={this.props.deleteDisabled}
                    size="sm"
                    onClick={this.props.onDelete}>

                    Delete

                </Button>
            </div>
        )
    }

}
